export class Indicator {
    id:number;
    label:string;
    value:number;

    maxValue:number;
    textValue:string;
    sidebar:boolean;

    customColor:string;

    constructor (id, label, value, maxValue, textValue, sidebar, customColor){
        this.id = id;
        this.label = label;
        this.value = value;
    
        this.maxValue = maxValue;
        this.textValue = textValue;
        this.sidebar = sidebar;
        this.customColor = customColor;
    }
}