import { Component, OnInit } from '@angular/core';
import { BatteryService } from 'src/app/services/battery.service';
import { ConfigService } from 'src/app/services/config.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-site',
  templateUrl: './site.component.html',
  styleUrls: ['./site.component.scss']
})
export class SiteComponent implements OnInit {

  checked1: false;
  value1: 10;

  checked2: false;
  value2: 10;

  checked3: false;
  value3: 10;

  constructor(public service:BatteryService, public configService:ConfigService, private router: Router ) { }

  ngOnInit() {
    this.configService.data.title="GSW-Office SP";
  }

  gotoString(str){
    this.service.currentString = str;
    this.router.navigateByUrl('/string');
  }

}
