import { Component, OnInit } from '@angular/core';
import { BatteryService } from 'src/app/services/battery.service';
import { ConfigService } from 'src/app/services/config.service';
import { Router } from '@angular/router';
import { Agent } from 'src/app/models/agent';

@Component({
  selector: 'app-string',
  templateUrl: './string.component.html',
  styleUrls: ['./string.component.scss']
})
export class StringComponent implements OnInit {

  public agents: Array<any> = [];
  maxAgents:number = 15;

  constructor(public service:BatteryService, public configService:ConfigService, private router: Router ) { }

  ngOnInit() {
    this.configService.data.title="GSW-Office SP";

    if (!this.service.currentString){
      this.service.currentString = this.service.strings[0];
    }

    this.generateAgents();
  }

  goSite(){
    this.router.navigateByUrl('/site');
  }


  generateAgents() {
    this.agents = [];

    for (let index = 0; index < this.maxAgents; index++) {
      let statusId =  Math.round(Math.random() * (this.configService.statusList.length -1)) ;

      let agent = new Agent(
        index,
        "J "+ (index+1),
        this.configService.statusList[statusId]
      )

      agent.svgId = Math.round(Math.random() * 3) ;

      this.agents.push(agent);
    }
    setInterval(() => this.updateAgents(), 10000);
  }

  updateAgents() {
    for (let index = 0; index < this.maxAgents; index++) {

      let statusId =  Math.round(Math.random() * (this.configService.statusList.length -1)) ;
      // let status = this.configService.statusList[statusId];

      let agent = this.agents[index];

      if (Math.round(Math.random() * 3) == 0){
        agent.status = this.configService.statusList[statusId];
      }

    }
  }

}
