import { Component, OnInit } from '@angular/core';
import { ConfigService } from 'src/app/services/config.service';
import { Indicator } from 'src/app/models/indicator';
import { Agent } from 'src/app/models/agent';

@Component({
  selector: 'app-home2',
  templateUrl: './home2.component.html',
  styleUrls: ['./home2.component.scss']
})
export class Home2Component implements OnInit {

  constructor(private configService:ConfigService ) {
    this.configService.loadConfig();
  }


  public agents: Array<any> = [];

  public graphics: Array<any> = [
    new Indicator(9, "Voltagem", 24, 36, "220v", true, "#AEE239"),
    new Indicator(10, "Carga total", 30, 100, "60h", true, "#F8CA00"),
    new Indicator(11, "Descarregadas", 1, 100, "", true, "#FF00AA"),
  ];


  ngOnInit(){

    // this.configService.saveConfig();


      // setInterval(() => this.updateAgents(), 10000);
      this.generateAgents();
  }

  generateAgents() {
    this.agents = [];

    for (let index = 0; index < 15; index++) {
      let statusId =  Math.round(Math.random() * (this.configService.statusList.length -1)) ;

      let agent = new Agent(
        index,
        "Site "+ index,
        this.configService.statusList[statusId]
      )

      agent.svgId = Math.round(Math.random() * 3) ;

      this.agents.push(agent);
    }
  }

  updateAgents() {
    for (let index = 0; index < 30; index++) {

      let statusId =  Math.round(Math.random() * (this.configService.statusList.length -1)) ;
      // let status = this.configService.statusList[statusId];

      let agent = this.agents[index];

      if (Math.round(Math.random() * 3) == 0){
        agent.status = this.configService.statusList[statusId];
      }

    }
  }

}
