import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { MapComponent } from './screens/map/map.component';
import { SiteComponent } from './screens/site/site.component';
import { StringComponent } from './screens/string/string.component';
import { BatteryComponent } from './screens/battery/battery.component';
import { Home2Component } from './screens/home2/home2.component';

const routes: Routes = [
  { path: '',        component: MapComponent },
  { path: 'site',    component: SiteComponent },
  { path: 'string',    component: StringComponent },
  { path: 'battery',    component: BatteryComponent },
  { path: 'home2',    component: Home2Component },
];


@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }


